.App {
  text-align: center;
  
}

.heading{
  font-weight: 900;
  font-size: 2em;
  text-align: center;
  color:teal;
}

.button{
  margin-left: 20px;
  background-color: orange;
  color: white;
  font-style: italic;
  font-size: large;
  height: 50px;
  width: 80px;
  border-radius: 20px;
}

@media screen and (max-width: 1060px) {
  #primary { width:67%; }
  #secondary { width:30%; margin-left:3%;}  
}
/* Tabled Portrait */
@media screen and (max-width: 768px) {
  #primary { width:100%; }
  #secondary { width:100%; margin:0; border:none; }
}

.elementContainer{
  text-align: center;
}



