.text-field{
    width: 30%;
    height: 50px;
    text-align: left;
    font-weight: bolder;
    background-color: lightblue;
    color:black;
    font-style: italic;
    font-size: large;
    padding-left: 3% ;
    padding-right: 3%;
    border-radius: 20px;
}

.elementBody{
    background-color: teal;
    width: 50%;
    height: 55px;
    text-align: center;
    margin-top: 20px;
    margin-left: 25%;
    border-radius: 20px;
    color:white;
    font-weight: bolder;
    vertical-align: middle;
    float: left;
    


}

.elementContent{
    
}

@media screen and (max-width: 1060px) {
    #primary { width:67%; }
    #secondary { width:30%; margin-left:3%;}  
  }
  /* Tabled Portrait */
  @media screen and (max-width: 768px) {
    #primary { width:100%; }
    #secondary { width:100%; margin:0; border:none; }
  }


